@import url(https://fonts.googleapis.com/css2?family=Fjalla+One&family=Source+Sans+Pro&display=swap);
.Footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  font-size: 0.8em;
  background-color: var(--tertiary-100);
  box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.06);
  color: white;
  padding: 0.5em 0;
}

.Footer-link {
  text-decoration: underline;
  color: white;
}

.Layout {
  width: 100vw;
  height: 100%;
}

.Layout-app {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.Header {
  background-color: var(--tertiary-100);
  box-shadow: inset 0 2px 4px 0 rgba(0, 0, 0, 0.06);
  padding: 1em 0;
}

.Header-title {
  color: #1a202c;
  font-size: 2.5em;
  text-align: center;
}

.Header-ven {
  color: var(--primary-500);
}

.Header-ezu {
  color: var(--tertiary-300);
}

.Header-ela {
  color: var(--secondary-300);
}

.ContainerChart {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 80%;
  height: var(--almost-chart);
  margin-bottom: 1em;
}

.ContainerCharts {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  background-color: var(--primary-300);
}

.sk-cube-grid {
  width: 60px;
  height: 60px;
}

.sk-cube-grid .sk-cube {
  width: 33%;
  height: 33%;
  background-color: #333;
  float: left;
  -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
  animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
}
.sk-cube-grid .sk-cube1 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
.sk-cube-grid .sk-cube2 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
.sk-cube-grid .sk-cube3 {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s;
}
.sk-cube-grid .sk-cube4 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}
.sk-cube-grid .sk-cube5 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}
.sk-cube-grid .sk-cube6 {
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s;
}
.sk-cube-grid .sk-cube7 {
  -webkit-animation-delay: 0s;
  animation-delay: 0s;
}
.sk-cube-grid .sk-cube8 {
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}
.sk-cube-grid .sk-cube9 {
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s;
}

@-webkit-keyframes sk-cubeGridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3D(1, 1, 1);
  }
  35% {
    transform: scale3D(0, 0, 1);
  }
}

@keyframes sk-cubeGridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3D(1, 1, 1);
  }
  35% {
    transform: scale3D(0, 0, 1);
  }
}

.PageLoading {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: var(--secondary-100);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Source Sans Pro";
  overflow-x: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Fjalla One";
}

:root {
  --primary-100: #fff1b4;
  --primary-200: #ffe888;
  --primary-300: #ffe163;
  --primary-400: #fcd842;
  --primary-500: #e8bf13;
  --secondary-100: #f3667a;
  --secondary-200: #e03c53;
  --secondary-300: #cc142d;
  --secondary-400: #a4061c;
  --secondary-500: #7e0011;
  --tertiary-100: #0c3bbb;
  --tertiary-200: #072d95;
  --tertiary-300: #04247b;
  --tertiary-400: #021b5f;
  --tertiary-500: #01113e;
  --fourty-100: #fafafb;
  --fourty-200: #dfe1eb;
  --fourty-300: #b9bed4;
  --fourty-400: #8a92b4;
  --fourty-500: #5f6997;
  --almost: calc(100vh - 2.5rem);
  --almost-chart: 70vh;
}

#app {
  position: absolute;
  z-index: 20;
  width: 100vw;
}

